<!--
 * @Description: 检查站信息
 * @Autor: YML
 * @Date: 2021-05-08 09:32:56
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 16:01:48
-->
<template>
  <div class="checkpoint">
    <box>
      <!-- 头部信息 start -->
      <div class="head">
        <div class="title-box">
          <div class="title">填写相关信息</div>
        </div>
        <div class="line-box">
          <div class="triangle"></div>
        </div>
      </div>
      <!-- 头部信息 end -->
      <!-- 表单信息 start -->
      <a-form-model layout="inline" class="form-main">
        <a-form-model-item label="检查站名称：">
          <a-input
            class="default-input"
            placeholder="请输入检查站名称"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item label="编码：">
          <a-input
            class="default-input"
            placeholder="请输入编码"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item label="站长：">
          <a-input
            class="default-input"
            placeholder="请输入站长名称"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item label="站长身份证号：">
          <a-input
            class="default-input"
            placeholder="请输入站长身份证号"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item
          label="上级部门："
          :getPopupContainer="triggerNode => triggerNode.parentNode"
        >
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选上级上级部门"
            :disabled="isModify"
          >
            <a-select-option value="jack">
              上级部门1
            </a-select-option>
            <a-select-option value="lucy">
              上级部门2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item
          label="检查站类型："
          :getPopupContainer="triggerNode => triggerNode.parentNode"
        >
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选上级检查站类型"
            :disabled="isModify"
          >
            <a-select-option value="jack">
              检查站类型1
            </a-select-option>
            <a-select-option value="lucy">
              检查站类型2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="联系方式：">
          <a-input
            class="default-input"
            placeholder="请输入联系方式"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item label="网络信息：">
          <a-input
            class="default-input"
            placeholder="请输入网络信息"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item label="地理坐标：">
          <a-input
            class="default-input"
            placeholder="请输入经纬度"
            :disabled="isModify"
            allow-clear
          />
        </a-form-model-item>
        <a-form-model-item label="备注">
          <a-textarea
            class="default-textarea"
            allow-clear
            placeholder="请输入信息"
            :disabled="isModify"
          />
        </a-form-model-item>
      </a-form-model>
      <!-- 表单信息 end -->
      <!-- 保存和重置按钮 start -->
      <div class="btn-box">
        <a-button type="primary" class="form-button" v-show="isModify" @click="toModify()"
          >修改</a-button
        >
        <a-button type="default" class="form-button" v-show="!isModify" @click="toCancel()"
          >取消</a-button
        >
        <a-button type="primary" class="form-button" v-show="!isModify" @click="toConfirm()"
          >确定</a-button
        >
      </div>
      <!-- 保存和重置按钮 end -->
    </box>
  </div>
</template>

<script>
import Box from "../../components/Box"; //边框样式组件
export default {
  components: {
    Box
  },
  data() {
    return {
      isModify: true
    };
  },
  methods: {
    /**
     * 点击修改
     */
    toModify() {
      this.isModify = false;
    },
    /**
     * 点击取消
     */
    toCancel() {
      this.isModify = true;
    },
    /**
     * 点击确定
     */
    toConfirm() {}
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
